import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Dc from '@/components/projects/Dc'
import Modimo from '@/components/projects/Modimo'
import Uni from '@/components/projects/Uni'

import TagDetail from '@/components/TagDetail'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/portfolio/',
      name: 'Home',
      component: Home
    },
    {
      path: '/portfolio/projects/dc',
      name: 'Dc',
      component: Dc
    },
    {
      path: '/portfolio/projects/modimo',
      name: 'Modimo',
      component: Modimo
    },
    {
      path: '/portfolio/projects/uni',
      name: 'Uni',
      component: Uni
    },

    // Tags
    {
      path: '/portfolio/tags/:name',
      name: 'TagDetail',
      component: TagDetail
    },

  ]
})



